@media screen and (max-width: 500px) {
    nav {
        position: fixed;
        @include flex-wrap( wrap, column, start, center );
        @include rounded-bot( desktop );
        background: rgba(26, 26, 26, 0.60);
        backdrop-filter: blur(8px);
        height: 80px;
        width: 100%;
        z-index: 999;
        gap: 16px;
        transition: 0.5s ease-in-out;
        overflow: hidden;
    
        .nav-left {
            width: calc(100% - 32px);
            height: 80px;
            padding-left: 0;
            padding: 0 16px;
            @include flex-wrap( nowrap, row, space-between, center );
            
            .nav-logo {
                height: 48px;
            }
            .nav-burger, .nav-close {
                display: flex;
                width: 48px;
                transition: 0.2s ease-in-out;
            }
            .nav-burger {
                position: absolute;
            }
            .mobile-icon-hide {
                opacity: 0;
            }
        }
        .nav-mid {
            @include flex-wrap( wrap, column, center, center );
            flex-grow: 0;
            gap: 0;
            
            .link {
                color: white;
    
                p {
                    font-size: $md;
                }
            }
        }
        .nav-right {
            @include flex-wrap( wrap, row, center, center );
            gap: $sm;
            padding-right: 0;
        }
    }
    .nav-open {
        height: 100vh;
    }
}