body {
    margin: 0;
    background: black;
}
.app {
    width: 100%;
    position: relative;
}
html {
    scroll-behavior: smooth;
}

main {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $md;
    padding: 0 80px;
}

%margin-desktop {
    padding: 0 80px;
}

@media screen and (max-width: 500px) {
    main {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: $md;
        padding: 0 16px;
    }
}