@media screen and (max-width: 500px) {
    .gallery-section {
        grid-column: 1 / span 4;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 16px;
    
        .gallery-text {
            @include flex-wrap( nowrap, column, center, baseline );
            gap: 8px;
            background-color: $grey;
            border-radius: 24px;
            grid-column: span 4;
            padding: 36px;
            h3,
            p {
                margin: 0;
            }
        }
    
        .gallery-image {
            border-radius: 24px;
            grid-column: span 2;
            overflow: hidden;
            aspect-ratio: 1 / 1;
    
            img {
                width: 100%;
                height: 100%;
            }
        }
    }
}