@media screen and (max-width: 500px) {
    .hero {
        grid-column: 1 / span 4;
        height: 95vh;
        margin: 0 -16px;
        position: relative;
    
        > img {
            @include rounded-bot( desktop );
            position: relative;
            width: 100%;
            height: 100%;
            object-position: center;
            object-fit: cover;
        }
    
        > div {
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 100%;
            display: grid;
            place-items: end center;
            
            > img {
                width: 50%;
            }
    
            > .button-container {
                place-self: end;
                position: relative;
                @include flex-wrap( wrap, column, center, center );
                width: 100%;
                height: auto;
                gap: $md;
                margin-bottom: 24px;

                .button {
                    width: calc(100% - 32px);
                    padding: 16px 0;
                    text-align: center;
                }
            }
        }
    }
}